*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  color-scheme: dark;
}

body {
  margin: 0;
  font-family: system-ui;
  font-size: 1.125rem;
  line-height: 1.6;
}

img {
  min-width: 100%;
  display: block;
}

.wrapper {
  min-width: 50rem;
  margin-inline: auto;
  padding-inline: 1rem;
}

.accordion {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.accordion__panel {
  position: relative;
}

.accordion__image {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

.accordion__trigger {
  background-color: transparent;
  border: 0;
}

.accordion__icon {
  fill: var(--_panel-color);
  background: hsl(0 0% 0% / .55);
  width: 3rem;
  aspect-ratio: 1;
  border-radius: 50%;
  padding: 0.75rem;
}

.accordion__panel {
  --_panel-color: red;
}